import Vue from 'vue'
import Axios from 'axios'
import { getTurnUrl } from '@/utils/env'

const http = Axios.create({
	// apibase_url
	baseURL: '/api/',
	timeout: 500000
})

// 
http.interceptors.request.use(config => {
	let sessionId = sessionStorage.getItem('sessionId')
	if(sessionId !== null) {
		let token = sessionStorage.getItem('token')
		config.headers['token'] = token
	}
	return config
}, error => {
	console.log(error)
	Promise.reject(error)
})

// response
http.interceptors.response.use((response) => {
	const data = response.data
	// if (data.code === 30001) {
  //   window.location.href =
  //     getTurnUrl() + '/userLogin' + '&response_type=code&scope=snsapi_base&state=1#wechat_redirect'
	// 	return
	// }

	if (data.code === 10000) {
		return data
	}
	
	if(data.msg) {
		Vue.$za.tip.open({
			text: data.msg
		})
	}
	

	// code
	const err = new Error(data.msg)

	err.data = data
	err.response = response
	throw err
}, (err) => {
	// 200
	if (err && err.response) {
		switch (err.response.status) {
			case 400:
				err.message = ''
				Vue.$za.tip.open({
					text: err.msg
				})
				break

			case 403:
				err.message = ''
				Vue.$za.tip.open({
					text: err.message
				})
				break

			case 404:
				err.message = `: ${err.response.config.url}`
				Vue.$za.tip.open({
					text: err.message
				})
				break

			case 408:
				err.message = ''
				Vue.$za.tip.open({
					text: err.message
				})
				break

			case 500:
				err.message = ''
				Vue.$za.tip.open({
					text: err.message
				})
				break

			default:
		}
	}

	return Promise.reject(err)
})

export default http
