// 
export function isEmpty(str) {
  if (str === '') {
    return false
  }
  return true
}

// 
export function isPhone(str) {
  const reg = /0?(1)[0-9]{10}/
  return reg.test(str)
}

// 6
export function isSixNumberCode(str) {
	var reg = /^[0-9]{6}$/
	return reg.test(str)
}

// 
export function isIdCard(str) {
  const vcity = {
    11: "", 12: "", 13: "", 14: "", 15: "",
    21: "", 22: "", 23: "", 31: "", 32: "",
    33: "", 34: "", 35: "", 36: "", 37: "", 41: "",
    42: "", 43: "", 44: "", 45: "", 46: "", 50: "",
    51: "", 52: "", 53: "", 54: "", 61: "", 62: "",
    63: "", 64: "", 65: "", 71: "", 81: "", 82: "", 91: ""
  }

  function checkCard(card) {
    //
    if (card === '') {
      return false
    }
    //
    if (isCardNo(card) === false) {
      return false
    }
    //
    if (checkProvince(card) === false) {
      return false
    }
    //
    if (checkBirthday(card) === false) {
      return false
    }
    //
    if (checkParity(card) === false) {
      return false
    }
    return true
  }

  //
  function isCardNo(card) {
    //1518151817X
    const reg = /(^\d{15}$)|(^\d{17}(\d|X)$)/
    if (reg.test(card) === false) {
      return false
    }

    return true
  }

  //,
  function checkProvince(card) {
    let province = card.substr(0, 2)
    if (vcity[province] == undefined) {
      return false
    }
    return true
  }

  //
  function checkBirthday(card) {
    let len = card.length
    //15332223
    if (len == '15') {
      let re_fifteen = /^(\d{6})(\d{2})(\d{2})(\d{2})(\d{3})$/
      let arr_data = card.match(re_fifteen)
      let year = arr_data[2]
      let month = arr_data[3]
      let day = arr_data[4]
      let birthday = new Date('19' + year + '/' + month + '/' + day)
      return verifyBirthday('19' + year, month, day, birthday)
    }
    //18334224X
    if (len == '18') {
      let re_eighteen = /^(\d{6})(\d{4})(\d{2})(\d{2})(\d{3})([0-9]|X)$/
      let arr_data = card.match(re_eighteen)
      let year = arr_data[2]
      let month = arr_data[3]
      let day = arr_data[4]
      let birthday = new Date(year + '/' + month + '/' + day)
      return verifyBirthday(year, month, day, birthday)
    }
    return false
  }

  //
  function verifyBirthday(year, month, day, birthday) {
    let now = new Date()
    let now_year = now.getFullYear()
    //
    if (birthday.getFullYear() == year && (birthday.getMonth() + 1) == month && birthday.getDate() == day) {
      //3100)
      let time = now_year - year
      if (time >= 3 && time <= 100) {
        return true
      }
      return false
    }
    return false
  }

  //
  function checkParity(card) {
    //1518
    card = changeFivteenToEighteen(card)
    let len = card.length
    if (len == '18') {
      let arrInt = new Array(7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2)
      let arrCh = new Array('1', '0', 'X', '9', '8', '7', '6', '5', '4', '3', '2')
      let cardTemp = 0
      let i
      let valnum

      for (i = 0; i < 17; i++) {
        cardTemp += card.substr(i, 1) * arrInt[i]
      }
      valnum = arrCh[cardTemp % 11]
      if (valnum == card.substr(17, 1)) {
        return true
      }
      return false
    }
    return false
  }

  //1518
  function changeFivteenToEighteen(card) {
    if (card.length == '15') {
      let arrInt = new Array(7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2)
      let arrCh = new Array('1', '0', 'X', '9', '8', '7', '6', '5', '4', '3', '2')
      let cardTemp = 0
      let i
      card = card.substr(0, 6) + '19' + card.substr(6, card.length - 6)
      for (i = 0; i < 17; i++) {
        cardTemp += card.substr(i, 1) * arrInt[i]
      }
      card += arrCh[cardTemp % 11]
      return card
    }
    return card
  }

  return checkCard(str)
}

//  
export function isWechat() {
  let u = navigator.userAgent
  let wechat = u.indexOf('MicroMessenger') > -1
  return wechat
}
